@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #011c40;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.page {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #011c40;
}

.page__body {
  background-color: #f9fbfd;
}
.page__body h1 {
  font-size: 54px;
  line-height: 1.2;
  font-weight: 700;
}
.page__body h2 {
  font-size: 28px;
  line-height: 1.5;
  font-weight: 600;
}
.page__body h3 {
  font-size: 18px;
  line-height: 1.5;
  font-weight: 500;
}
@media (max-width: 1023px) {
  .page__body h1 {
    font-size: 50px;
  }
  .page__body h2 {
    font-size: 26px;
  }
  .page__body h3 {
    font-size: 18px;
  }
}
@media (max-width: 767px) {
  .page__body h1 {
    font-size: 36px;
    line-height: 1.25;
  }
  .page__body h2 {
    font-size: 24px;
  }
  .page__body h3 {
    font-size: 18px;
  }
}

.page__wrapper {
  min-width: 290px;
  max-width: 1160px;
  margin: 0 auto;
}

.page__header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 2;
}

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 14px 20px 15px;
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 600;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.button.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.logo {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.logo.focus-visible:focus {
  opacity: 0.8;
}

.logo__image {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .logo__image {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__image {
    width: 42px;
    height: 22px;
  }
}

.navigation {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .navigation {
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .navigation {
    flex-direction: column;
    margin-left: 0;
  }
}

.navigation__link {
  margin-bottom: 10px;
  margin-left: 33px;
  line-height: 1.2;
  font-weight: 500;
  color: #f9fbfd;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .navigation__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.navigation__link.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .navigation__link {
    margin-left: 23px;
    font-size: 12px;
  }
}
@media (max-width: 767px) {
  .navigation__link {
    margin-top: 23px;
    margin-bottom: 0;
    margin-left: 0;
  }
}

@media (max-width: 1023px) {
  .navigation--header .navigation__link {
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .navigation--header .navigation__link {
    font-size: 18px;
  }
}

form {
  display: grid;
  row-gap: 20px;
  padding: 50px;
  font-size: 10px;
  line-height: 1.3;
  color: #f9fbfd;
  background-color: #011c40;
}
form h3 {
  margin-top: 0;
  margin-bottom: 15px;
}
form input {
  width: 100%;
  padding: 11px 16px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  border: none;
  transition: all 0.3s ease;
}
form input::placeholder {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.5;
  color: rgba(1, 28, 64, 0.7);
}
@media (hover: hover) {
  form input:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
form input[type=checkbox] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
form input[type=checkbox] + span, form input[type=checkbox] + span + span {
  cursor: pointer;
}
form input[type=checkbox] + span {
  min-width: 18px;
  height: 18px;
  margin-right: 5px;
  background-color: #f9fbfd;
  transition: all 0.3s ease;
}
@media (hover: hover) {
  form input[type=checkbox]:hover:not(.focus-visible) + span {
    opacity: 0.6;
  }
}
form input[type=checkbox]:checked + span {
  background-image: url("../img/svg/checkbox.svg");
  background-size: 12px 8px;
  background-repeat: no-repeat;
  background-position: center;
}
form .checkbox-label {
  display: flex;
  align-items: center;
}
form button {
  display: block;
  width: 100%;
  margin-top: 10px;
  padding: 14px 20px 15px;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 600;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
form button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  form button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
form button.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 767px) {
  form {
    padding: 40px 30px;
  }
  form input[type=checkbox] + span {
    margin-right: 8px;
  }
}

.header {
  padding: 35px 40px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .header {
    padding: 30px 45px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 20px 15px;
  }
  .header--navigation-opened {
    min-height: 568px;
    padding: 0;
    background-color: rgba(0, 43, 102, 0.5);
  }
  .header--navigation-nojs {
    position: relative;
    padding: 0;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.header__navigation-toggle {
  display: none;
}

@media (max-width: 767px) {
  .header--navigation-opened .header__wrapper,
.header--navigation-nojs .header__wrapper {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: repeat(2, auto);
    align-content: start;
    row-gap: 33px;
    min-height: 568px;
    margin-left: 35px;
    padding: 20px 15px;
    background-color: #f9fbfd;
  }
  .header--navigation-nojs .header__wrapper {
    min-height: 265px;
    margin-left: 0;
  }
  .header--navigation-opened .header__logo,
.header--navigation-nojs .header__logo {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .header--navigation-opened .header__logo svg,
.header--navigation-nojs .header__logo svg {
    fill: #011c40;
  }
  .header__navigation-toggle {
    position: relative;
    display: block;
    width: 24px;
    height: 22px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer;
    transition: opacity 0.3s ease;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header__navigation-toggle:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .header__navigation-toggle.focus-visible:focus {
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .header--navigation-opened .header__navigation-toggle {
    grid-column: -1/-2;
    grid-row: 1/2;
  }
}
@media (max-width: 767px) {
  .header--navigation-nojs .header__navigation-toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .header--navigation-opened .header__navigation-toggle::before,
.header--navigation-opened .header__navigation-toggle::after {
    position: absolute;
    top: 2px;
    right: 0;
    bottom: 2px;
    left: 0;
    width: 1.5px;
    margin: auto;
    content: "";
    background-color: #011c40;
    transform: rotate(45deg);
  }
}
@media (max-width: 767px) {
  .header--navigation-opened .header__navigation-toggle::after {
    transform: rotate(-45deg);
  }
}
@media (max-width: 767px) {
  .header--navigation-closed .header__navigation-toggle::before {
    position: absolute;
    z-index: 10;
    top: 4px;
    right: 0;
    left: 0;
    height: 2px;
    content: "";
    background-color: #f9fbfd;
    box-shadow: 0 6px 0 0 #f9fbfd, 0 12px 0 0 #f9fbfd;
  }
}
@media (max-width: 767px) {
  .header__navigation {
    grid-column: 1/-1;
    grid-row: 2/3;
    margin-top: 0;
    margin-right: 15px;
  }
  .header__navigation a {
    margin-top: 0;
    margin-bottom: 10px;
    padding: 10px;
    color: #011c40;
  }
}
@media (max-width: 767px) {
  .header--navigation-closed .header__navigation {
    display: none;
  }
}
.footer {
  padding: 50px 40px;
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .footer {
    padding: 50px 45px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 40px 15px;
  }
}

.footer__wrapper {
  display: flex;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
  }
}

.footer__logo {
  margin-right: 7px;
}
@media (max-width: 1023px) {
  .footer__logo {
    margin-right: 2px;
  }
}

.footer__navigation {
  flex-grow: 1;
  margin-top: 13px;
}
@media (max-width: 1023px) {
  .footer__navigation {
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .footer__navigation {
    margin-top: 7px;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: flex-start;
  min-width: 160px;
  margin-top: 11px;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .footer__social-list {
    min-width: 115px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    justify-content: flex-start;
    margin-top: 30px;
    margin-left: 0;
  }
}

.footer__social-link {
  display: flex;
  align-items: flex-start;
  margin-left: 27px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__social-link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.footer__social-link.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 1023px) {
  .footer__social-link {
    margin-left: 22px;
  }
}
@media (max-width: 767px) {
  .footer__social-link:first-child {
    margin-left: 0;
  }
}

.footer__social-icon {
  height: 22px;
}
@media (max-width: 1023px) {
  .footer__social-icon {
    height: 16px;
  }
  .footer__social-icon--ok {
    width: 9.6px;
  }
  .footer__social-icon--zen {
    width: 16.4px;
  }
  .footer__social-icon--reddit {
    width: 16.4px;
  }
}

.promo {
