  }
}
@media (max-width: 1023px) {
  .promo {
    min-height: 638px;
    padding: 0 45px;
    font-size: 18px;
    box-shadow: none;
  }
}
@media (max-width: 768px) {
  .promo {
    background-image: url("../img/promo-tablet.jpg");
  }
}
@media (max-width: 768px) and (min-resolution: 144dpi), (max-width: 768px) and (min-resolution: 1.5dppx) {
  .promo {
    background-image: url("../img/promo-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .promo {
    min-height: 500px;
    padding: 0 15px;
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  .promo {
    background-image: url("../img/promo-mobile.jpg");
  }
}
@media (max-width: 375px) and (min-resolution: 144dpi), (max-width: 375px) and (min-resolution: 1.5dppx) {
  .promo {
    background-image: url("../img/promo-mobile@2x.jpg");
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  min-height: 650px;
  padding-bottom: 283px;
}
.promo__wrapper p {
  margin: 0 0 0 95px;
  line-height: 1.2;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo__wrapper {
    min-height: 638px;
    padding-bottom: 280px;
  }
  .promo__wrapper p {
    margin-left: 0;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .promo__wrapper {
    min-height: 500px;
    padding-bottom: 185px;
  }
  .promo__wrapper p {
    padding-right: 60px;
    line-height: 1.3;
  }
}

.promo__title {
  margin: 0 0 30px 95px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo__title {
    margin: 0 0 29px;
    text-shadow: none;
  }
}
@media (max-width: 767px) {
  .promo__title {
    margin-bottom: 25px;
  }
}

