.advantages {
  min-height: 562px;
  padding: 120px 40px;
  background-image: url("../img/advantages-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 14px;
}
@media (min-width: 1024px) and (min-resolution: 144dpi), (min-width: 1024px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/advantages-desktop@2x.jpg");
  }
}
.advantages h2 {
  margin-top: 0;
  margin-bottom: 60px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages {
    min-height: 588px;
    padding: 100px 45px;
    background-image: url("../img/advantages-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/advantages-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .advantages {
    padding: 65px 15px;
    background-image: url("../img/advantages-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/advantages-mobile@2x.jpg");
  }
}
@media (max-width: 767px) {
  .advantages h2 {
    margin-bottom: 40px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 40px;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .advantages__list {
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
    column-gap: 0;
    row-gap: 30px;
  }
}

.advantages__item {
  position: relative;
  padding: 87px 42px 50px 30px;
  background-color: #f9fbfd;
}
.advantages__item h3 {
  margin-top: 0;
  margin-bottom: 20px;
}
.advantages__item p {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.4;
}
.advantages__item::before {
  position: absolute;
  top: 30px;
  left: 30px;
  content: "";
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.advantages__item--liner::before {
  width: 27px;
  height: 32px;
  background-image: url("../img/svg/liner.svg");
}
.advantages__item--inclusive::before {
  width: 38px;
  height: 32px;
  background-image: url("../img/svg/all-inclusive.svg");
}
.advantages__item--tours::before {
  width: 44px;
  height: 32px;
  background-image: url("../img/svg/tours.svg");
}
@media (max-width: 1023px) {
  .advantages__item {
    padding: 76px 16px 25px 20px;
  }
  .advantages__item::before {
    top: 26px;
    left: 20px;
  }
  .advantages__item--liner::before {
    width: 21px;
    height: 26px;
  }
  .advantages__item--inclusive::before {
    width: 27px;
    height: 26px;
  }
  .advantages__item--tours::before {
    width: 36px;
    height: 26px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    padding: 76px 15px 25px 20px;
  }
  .advantages__item::before {
    top: 25px;
  }
}

